<template>
  <el-dialog
    width="70%"
    :title="`更新${program}配置`"
    :visible.sync="dialogVisible"
    :destroy-on-close="true"
    :close-on-click-modal="false"
  >
    <codemirror
      v-model="code"
      :options="cmOptions"
      @input="onCmCodeChange"
    ></codemirror>
    <codemirror
      v-if="sendCode !== ''"
      v-model="sendCode"
      :options="cmOptions"
      @input="onCmCodeChange"
    ></codemirror>
    <span slot="footer" class="dialog-footer">
      <el-button
        @click="$emit('changeEvent', 'dialogVisible', false)"
        size="small"
        plain
        >取 消</el-button
      >
      <el-button type="primary" size="small" plain @click="save"
        >确 定</el-button
      >
    </span>
  </el-dialog>
</template>
<script>
import "codemirror/mode/javascript/javascript.js";
// theme css
import "codemirror/theme/base16-dark.css";
import { getConfig, saveConfig } from "@/common/index";
export default {
  props: {
    dialogVisible: {
      type: Boolean,
      default: false,
    },
    program: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      code: "",
      sendCode: "",
      cmOptions: {
        tabSize: 4,
        mode: "text/javascript",
        theme: "base16-dark",
        lineNumbers: true,
        line: true,
      },
    };
  },
  watch: {
    program() {
      this.code = "";
      this.sendCode = "";
      getConfig(this.program).then((res) => {
        this.code = `${res.data.config}`;
        this.sendCode = `${res.data.sendConfig || ""}`;
      });
    },
  },
  methods: {
    onCmReady(cm) {
      console.log("the editor is readied!", cm);
    },
    onCmFocus(cm) {
      console.log("the editor is focus!", cm);
    },
    onCmCodeChange() {},
    save() {
      saveConfig(this.program, this.code).then(() => {
        if (this.sendCode) {
          saveConfig(this.program, this.sendCode, "1").then(() => {
            this.$message.success("保存成功");
            this.$emit("changeEvent", "dialogVisible", false);
          });
        } else {
          this.$message.success("保存成功");
          this.$emit("changeEvent", "dialogVisible", false);
        }
      });
    },
  },
};
</script>
<style>
.CodeMirror-code {
  text-align: left;
}
.vue-codemirror {
  height: 100%;
  background: #151515;
  min-height: 200px;
}
.CodeMirror {
  height: 100% !important;
}
.el-dialog__body {
  min-height: 200px;
  padding: 0 !important;
}
.el-dialog {
  background: #336996 !important;
}
.el-dialog__title {
  color: #ddd !important;
}
</style>
