import request from "@/common/request";

// 获取AI节点详情
export function getConfig(program) {
  return request({
    url: "/api/getConfig",
    method: "get",
    params: {
      program,
    },
  });
}
export function pm2Restart(id, type) {
  return request({
    url: "/pm2/pm2Restart",
    method: "get",
    params: {
      id,
      type,
    },
  });
}
export function pm2Stop(id, type) {
  return request({
    url: "/pm2/pm2Stop",
    method: "get",
    params: {
      id,
      type,
    },
  });
}
export function saveConfig(program, content, url = "0") {
  return request({
    url: "/api/saveConfig",
    method: "post",
    data: {
      program,
      content,
      url,
    },
  });
}
export function getPm2List() {
  return request({
    url: "/pm2/getPm2List",
    method: "get",
  });
}
