import axios from "axios";
import { Message } from "element-ui";
import qs from "qs";

const service = axios.create({
  timeout: 20000,
  responseType: "json",
});
// 请求拦截
service.interceptors.request.use(
  (config) => {
    // 针对有些config写的是大写
    config.method = config.method.toLowerCase();
    // 不是json请求格式的post和put做请求参数转换
    if (
      (config.method === "post" || config.method === "put") &&
      config.headers["Content-Type"] !== "application/json"
    ) {
      config.data = qs.stringify(config.data);
    }
    // config.baseURL = "http://127.0.0.1:7788";
    // config.baseURL = "http://10.68.137.30:11826/";
    // config.baseURL = "http://192.168.0.30:11826/";
    // const { protocol, hostname } = window.location;
    // config.baseURL = `${protocol}//${hostname}:11826`;
    config.baseURL = "";
    return config;
  },
  (err) => {
    return Promise.reject(err);
  }
);
// 响应拦截
service.interceptors.response.use(
  (response) => {
    const res = response.data;
    if (response.status !== 200) {
      const msg = res.message
        ? res.message.indexOf("timeout") > -1
          ? "请求超时，请稍后"
          : res.message
        : "网络异常";
      Message.error(msg);
      return Promise.reject(new Error(msg || "Error"));
    } else {
      return res;
    }
  },
  (error) => {
    let msg = error.message;
    msg =
      msg.indexOf("timeout") > -1 ? "请求超时，请稍后" : "网络异常请稍后重试";
    Message.error(msg);
    return Promise.reject(error.message);
  }
);
export default service;
